<template>
    <div>
        <button title="imprimir" @click="print" id="fixedbutton" class="ui icon button green"><i
                class="big print icon"></i></button>
        <button title="voltar" @click="tab('edit')" id="back" class="ui icon button green"><i
                class="big reply icon"></i></button>
        <div class="ui grid page">
            <div class="column">
                <div class="ui segment">
                    <h2 class="ui center aligned icon header">
                        <i class="file text icon orange"></i>
                        Editor de Procuração
                    </h2>
                    <div class="ui menu top">
                        <a @click="tab('edit')" class="active item blue" data-tab="edit"><i class="icon edit sign"></i>Edição</a>
                        <a @click="tab('doc')" class="item blue" data-tab="summary"><i class="file text icon"></i>Procuração</a>
                    </div>
                    <div id="edit" class="ui tab item active" data-tab="edit">
                        <div class="ui form">
                            <h4 class="ui dividing header red">Dados do Propritário</h4>
                            <div class="three fields">
                                <div class="field">
                                    <label>Nome</label>
                                    <input name="nome" maxlength="35" v-model="propi.nome" type="text"/></div>
                                <div class="field">
                                    <label>CPF</label>
                                    <input name="cpf" maxlength="14" v-model="propi.cpf" type="text"/></div>
                                <div class="field">
                                    <label>RG</label>
                                    <input name="rg" maxlength="11" v-model="propi.rg" type="text"/></div>
                            </div>
                            <div class="field">
                                <label>Rua/Av.</label>
                                <input name="rua" maxlength="35" v-model="propi.ende.logra" type="text"/></div>
                            <div class="three fields">
                                <div class="field">
                                    <label>Numero</label>
                                    <input name="numero" maxlength="5" v-model="propi.ende.num" type="text"/></div>
                                <div class="field">
                                    <label>Bairro</label>
                                    <input name="bairro" maxlength="30" v-model="propi.ende.bairro" type="text"/>
                                </div>
                                <div class="field">
                                    <label>Cidade</label>
                                    <input name="cidade" maxlength="20" v-model="propi.ende.cit" type="text"/></div>
                                <div class="field">
                                    <label>Estado</label>
                                    <input name="UF" maxlength="2" style="text-transform:uppercase" v-model="propi.ende.est" type="text"/>
                                </div>
                            </div>
                            <h4 class="ui dividing header red">Dados do Veículo</h4>
                            <div class="three fields">
                                <div class="field">
                                    <label>Marca</label>
                                    <input name="marca" maxlength="20" style="text-transform:uppercase" v-model="veic.marca" type="text"/></div>
                                <div class="field">
                                    <label>Chassi</label>
                                    <input name="chassi" maxlength="20" style="text-transform:uppercase" v-model="veic.chassi" type="text"/></div>
                                <div class="field">
                                    <label>Placa</label>
                                    <input name="placa" maxlength="8" style="text-transform:uppercase" v-model="veic.placa" type="text"/></div>
                            </div>
                            <div class="three fields">
                                <div class="field">
                                    <label>Renevam</label>
                                    <input name="renavam" maxlength="20" v-model="veic.renavan" type="text"/></div>
                                <div class="field">
                                    <label>Tipo</label>
                                    <input name="tipo" maxlength="15" style="text-transform:uppercase" v-model="veic.tipo" type="text"/></div>
                                <div class="field">
                                    <label>Cor</label>
                                    <input name="cor" maxlength="15" style="text-transform:uppercase" v-model="veic.cor" type="text"/></div>
                                <div class="field">
                                    <label>Ano/Modelo</label>
                                    <input name="ano" maxlength="9" v-model="veic.ano" type="text"/></div>
                            </div>
                            <h4 class="ui dividing header blue">Dados do Procurador</h4>
                            <div class="three fields">
                                <div class="field">
                                    <label>Nome</label>
                                    <input name="nome" maxlength="35" v-model="proc.nome" type="text"/></div>
                                <div class="field">
                                    <label>CPF</label>
                                    <input name="cpf" maxlength="14" v-model="proc.cpf" type="text"/></div>
                                <div class="field">
                                    <label>RG</label>
                                    <input name="rg" maxlength="11" v-model="proc.rg" type="text"/></div>
                            </div>
                            <div class="field">
                                <label>Rua/Av.</label>
                                <input name="rua" v-model="proc.ende.logra" type="text"/>
                            </div>
                            <div class="three fields">
                                <div class="field">
                                    <label>Numero</label>
                                    <input name="numero" maxlength="5" v-model="proc.ende.num" type="text"/></div>
                                <div class="field">
                                    <label>Bairro</label>
                                    <input name="bairro" maxlength="30" v-model="proc.ende.bairro" type="text"/></div>
                                <div class="field">
                                    <label>Cidade</label>
                                    <input name="cidade" maxlength="20" v-model="proc.ende.cit" type="text"/></div>
                                <div class="field">
                                    <label>Estado</label>
                                    <input name="UF" maxlength="2" style="text-transform:uppercase" v-model="proc.ende.est" type="text"/>
                                </div>
                            </div>
                            <h4 class="ui dividing header blue"></h4>
                            <button @click="tab('doc')" class="ui icon button green"><i class="write icon"></i> GERAR
                            </button>
                        </div>
                    </div>
                    <div id="doc" class="ui tab" data-tab="summary">
                        <div id="section-to-print">
                            <p align="center">
                                <strong>PROCURAÇÃO</strong>
                            </p>
                            <br><br>
                            <p class="recuo" style="text-align: justify">
                                Por este instrumento particular de procuração, eu, abaixo qualificado como
                                proprietário do veículo, também, qualifico e dou amplos e ilimitados
                                poderes, da referida Procuração, podendo o Procurador utilizar-se da mesma
                                para fins especiais de representar-me junto aos órgãos de Trânsito, no que
                                diz respeito a vender e dar quitação, solicitar segunda via do CRV-CRLV,
                                ficando o Procurador civil e criminalmente responsável pelo referido
                                veículo, por multas, IPVA, DPVAT, acidentes de trânsito, que venham
                                ocorrer.
                            </p>
                            <p>
                                <strong>PROPRIETÁRIO:</strong>
                                <br/>
                                NOME: {{ propi.nome }}
                                <br/>
                                CPF: {{ propi.cpf }}
                                <br/>
                                RG: {{ propi.rg }}
                                <br>
                                ENDEREÇO: {{ 'Rua/Av. ' + propi.ende.logra + ', Nº: ' + propi.ende.num + ', ' + propi.ende.bairro + ', ' + propi.ende.cit + ' – ' + propi.ende.est
                                }}.
                            </p>
                            <p>
                                <strong>PROCURADOR:</strong>
                                <br/>
                                NOME: {{ proc.nome }}
                                <br/>
                                CPF: {{ proc.cpf }}
                                <br/>
                                RG: {{ proc.rg }}
                                <br/>
                                ENDEREÇO: {{ 'Rua/Av. ' + proc.ende.logra + ', Nº: ' + proc.ende.num + ', ' + proc.ende.bairro + ', ' + proc.ende.cit + ' – ' + propi.ende.est
                                }}.
                            </p>
                            <p>
                                <strong>
                                    VEÍCULO:
                                    <br/>
                                </strong>
                                MARCA: {{ veic.marca.toUpperCase() }}
                                <br>
                                CHASSI: {{ veic.chassi.toUpperCase() }}
                                <br/>
                                PLACA: {{ veic.placa.toUpperCase() }}
                                <br>
                                RENAVAM: {{ veic.renavan }}
                                <br/>
                                TIPO: {{ veic.tipo.toUpperCase() }}
                                <br>
                                COR: {{ veic.cor.toUpperCase() }}
                                <br/>
                                MODELO: {{ veic.ano }}
                            </p>
                            <br><br><br>
                            <p class="recuo" style="text-align: justify">
                                Podendo o Procurador apresentar o veículo, bem como solicitar vistoria,
                                transferências, fazer declaração de Residência, alterar endereço de entrega
                                de Documentos, do CRV-CRLV, requerer troca de Estado e Município, retirar o
                                veículo do Depósito (CRD), retirar documentos junto aos Correios, podendo
                                ainda, dar baixa no referido veículo, transitar, com o referido veículo em
                                países que fazem fronteira com o Brasil, e ainda representar-me junto à
                                receita estadual. Podendo transferir para o seu próprio nome, ou a quem
                                quiser. Podendo substabelecer.
                            </p>
                            <br><br><br><br><br>
                            <p align="center">
                                <strong>___________________________________­­­_______</strong>
                            </p>
                            <p align="center">
                                {{ propi.nome }}<strong></strong>
                            </p>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
  export default {
    components: {},
    data () {
      return {
        propi: {
          nome: '',
          cpf: '',
          rg: '',
          ende: { logra: '', num: '', bairro: '', cit: '', est: '' }
        },
        proc: {
          nome: '',
          cpf: '',
          rg: '',
          ende: { logra: '', num: '', bairro: '', cit: '', est: '' }
        },
        veic: {
          marca: '',
          chassi: '',
          placa: '',
          renavan: '',
          tipo: '',
          cor: '',
          ano: ''
        }
      }
    },
    methods: {
      print () {
        window.print()
      },
      tab (e) {
        if (e === 'doc') {
          $('[data-tab="edit"]').removeClass('active')
          $('[data-tab="summary"]').addClass('active')
          $('#fixedbutton').show()
          $('#back').show()
        } else {
          $('[data-tab="edit"]').addClass('active')
          $('[data-tab="summary"]').removeClass('active')
          $('#fixedbutton').hide()
          $('#back').hide()
        }
      }
    }
  }
</script>

<style>
    body {
        background: #c5c9cc;
    }

    #back {
        display: none;
        border-radius: 10px;
        font-size: 14px;
        position: fixed;
        bottom: 25%;
        right: 2%;
        z-index: 1;
    }

    #fixedbutton {
        display: none;
        border-radius: 10px;
        font-size: 14px;
        position: fixed;
        bottom: 40%;
        right: 2%;
        z-index: 1;
    }

    #doc {
        /* box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19); */
        /* background: whitesmoke; */
        /* border: 0.5px solid rgba(0, 0, 0, 0.4); */
        padding: 10px;
        /* margin: 2px; */
    }

    @media print {
        body * {
            visibility: hidden;
        }

        #section-to-print,
        #section-to-print * {
            visibility: visible;
        }

        #section-to-print {
            position: absolute;
            left: 5px;
            right: 20px;
            top: 0;
        }
    }

    .recuo {
        text-indent: 4em;
    }

    h2 {
        text-align: center;
    }

    #app > div > div {
        padding-top: 2%;
    }
</style>


